
import {computed, defineComponent, onBeforeUpdate, PropType, Ref, ref} from "vue";
import vClickOutside from "click-outside-vue3";
import useKeyboardEventsHandler from "./useKeyboardEventsHandler";

export default defineComponent({
  name: "LanguageDropdown",
  directives: {
    clickOutside: vClickOutside.directive,
  },
  props: {
    items: {
      required: true,
      type: Array,
    },
    onChange: {
      required: true,
      type: Function as PropType<(value: string) => void>,
    },
    placeholder: {
      required: false,
      type: String,
      default: "",
    },
    currentOptionKey: {
      type: String || Number,
      required: false,
      default: null,
    },
  },
  setup(props, {emit}) {
    const areMenuItemsShown = ref(false);
    const menuItemsRefs: Ref<Array<HTMLElement>> = ref([]);
    const selectedItemRef = ref();
    onBeforeUpdate(() => {
      menuItemsRefs.value = [];
    });
    const setMenuItemRef = (el: HTMLElement) => {
      menuItemsRefs.value.push(el);
    };
    const { buttonKeyboardHandler, movePrev, moveNext, closeMenu } =
        useKeyboardEventsHandler(areMenuItemsShown, menuItemsRefs, selectedItemRef);

    const toggle = () => (areMenuItemsShown.value = !areMenuItemsShown.value);
    const onSelectItem = (item: any) => {
      props.onChange(item.key)
      areMenuItemsShown.value = false
    };

    window.addEventListener("scroll", function () {
      areMenuItemsShown.value = false
    });


    const iconArrowClass = computed(() => areMenuItemsShown.value ? "" : "language-dropdown__icon-arrow--open")

    return {areMenuItemsShown, closeMenu, buttonKeyboardHandler, movePrev, moveNext, toggle, setMenuItemRef, onSelectItem, menuItemsRefs, selectedItemRef, iconArrowClass}
  }
});
