import { emitter } from "./emitter";
import { App } from "vue";

class ModalPlugin {
  open() {
    emitter.emit("openModal");
  }
  close() {
    emitter.emit("closeModal");
  }
}

export default {
  install: (app: App): void => {
    app.config.globalProperties.$modal = new ModalPlugin();
  },
};

export const $modal = new ModalPlugin();
