
import {computed, defineComponent} from "vue";
import LanguageDropdown from "@/components/languageDropdown/LanguageDropdown.vue";
import projectModule from "@/store/project";

export default defineComponent({
  name: "Header",
  components: {
    LanguageDropdown
  },
  setup() {
    const menuData = [
      {
        title: "про нас",
        link: "about_us"
      },
      {
        title: "розклад",
        link: "schedule"
      },
      {
        title: "ціни",
        link: "prices"
      },
      {
        title: "запитання",
        link: "questions"
      },
      {
        title: "контакти",
        link: "contacts"
      },

    ]

    window.addEventListener("scroll", function () {
      const header = document.querySelector('header')
      header?.classList.toggle(
        "sticky",
        window.scrollY > 0
      );
      const logo = document.querySelector('img')

      window.scrollY > 0
        ? logo!.src = require("../../assets/images/logo.svg")
        : logo!.src = require("../../assets/images/logo.svg")
    });

    const languages = computed(() => projectModule.languages)
    const currentLanguageKey = computed(() => projectModule.currentLanguage)
    const onSelectLanguage = (value: string) => projectModule.currentLanguage = value

    return {
      menuData,
      languages,
      currentLanguageKey,
      onSelectLanguage
    }
  }
});
