
import { Swiper, SwiperSlide,  } from 'swiper/vue';
import SwiperCore, {
  Navigation,
  EffectCoverflow
} from 'swiper/core';
import { defineComponent, ref } from "vue";
SwiperCore.use([Navigation, EffectCoverflow]);

export default defineComponent({
  components: {
    Swiper,
    SwiperSlide,
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    initIndex: {
      type: Number,
      required: false,
      default: 0
    },
    centeredSlides: {
      type: Boolean,
      required: false,
      default: false,
    },
    slidesPerView: {
      type: Number,
      required: false,
      default: 1,
    },
    spaceBetween: {
      type: Number,
      required: false,
      default: 0,
    },
    navigation: {
      type: Boolean,
      required: false,
      default: true,
    },
    initialSlide: {
      type: Number,
      required: false,
      default: 0,
    },
    effect: {
      type: String,
      required: false,
      default: '',
    },
    coverflowEffectConfig: {
      type: Object,
      required: false,
      default: {
        rotate: 50,
        stretch: 0,
        depth: 200,
        modifier: 1,
        slideShadows: true,
      }
    }
  },
  setup() {
    const currentIndex = ref(1)
    const onChangeSlide = (value: any) => {
      const p = new Proxy(value, {});
      currentIndex.value = value.activeIndex + 1
    }

    return {
      onChangeSlide,
      currentIndex
    }
  }

})
