import { computed, Ref } from "vue";

export default function useKeyboardEventsHandler(
  areMenuItemsShown: Ref<boolean>,
  menuItemsRefs: Ref<Array<HTMLElement>>,
  selectedItemRef: Ref<HTMLInputElement>
) {
  const maxIndex = computed(() => menuItemsRefs.value.length - 1);
  const minIndex = 0;
  let currentMenuItem = -1;

  const buttonKeyboardHandler = (event: KeyboardEvent) => {
    const keyUpOrDown = event.key === "ArrowUp" || event.key === "ArrowDown";

    if (!areMenuItemsShown.value) {
      if(keyUpOrDown) {
        areMenuItemsShown.value = true;
      }
    } else {
      if (keyUpOrDown || event.key === "Tab") {
        currentMenuItem++;
        menuItemsRefs.value[currentMenuItem].focus();
        event.preventDefault();
      }
    }
  };

  const movePrev = (event: any) => {
    event.preventDefault();

    if (currentMenuItem > minIndex) {
      event.target.previousElementSibling.focus();
      currentMenuItem--;
    } else if (event.key === "Tab" && currentMenuItem === minIndex) {
      selectedItemRef.value.focus()
      closeMenu()
    }
  };

  const moveNext = (event: any) => {
    event.preventDefault();

    if (currentMenuItem < maxIndex.value) {
      event.target.nextElementSibling.focus();
      currentMenuItem++;
    } else if (event.key === "Tab" && currentMenuItem === maxIndex.value) {
      closeMenu();
    }
  };

  const closeMenu = () => {
    areMenuItemsShown.value = false;
    currentMenuItem = -1;
  };

  return {
    buttonKeyboardHandler,
    movePrev,
    moveNext,
    closeMenu,
  };
}
