
import {computed, defineComponent, ref} from "vue";
import Swiper from "@/components/swiper/Swiper.vue";
import CalendarItem from "@/components/sections/calendar/CalendarItem.vue";
import projectModule from "@/store/project";

export default defineComponent({
  name: "Calendar",
  components: {
    Swiper,
    CalendarItem
  },
  setup() {
    const year = ref('2023')
    const currentLanguage = computed(() => projectModule.currentLanguage)
    const config: any = {
      uk: {
        title: `календар Tango Secret ${year.value}р.`,
      },
      ru: {
        title: `календарь Tango Secret ${year.value}г.`,
      }
    }

  const coverflowEffectConfig = {
    rotate: 50,
      stretch: 0,
      depth: 200,
      modifier: 1,
      slideShadows: true,
  }

    return {config, coverflowEffectConfig, currentLanguage, year}
  }
});
